<script>
import DescriptionDisplay from "@/components/DescriptionDisplay";
import EffectDisplay from "@/components/EffectDisplay";
import PrimaryButton from "@/components/PrimaryButton";

export default {
  name: "ChallengeBox",
  props: {
    challenge: {
      type: Object,
      required: true
    }
  },
  components: {
    DescriptionDisplay,
    EffectDisplay,
    PrimaryButton
  },
  computed: {
    config() {
      return this.challenge.config;
    },
    reward() {
      return this.challenge.reward.config;
    },
    name() {
      return this.config.name;
    },
    resetType() {
      return this.config.type.name;
    }
  }
}
</script>

<template>
  <div class="o-challenge-box">
    <div class="o-challenge-image-and-title-row">
      <img
        :src="`images/challenges/${challenge.id}.png`"
        class="c-challenge-image"
      >
      <div class="c-challenge-info">
        <div class="c-challenge-name">{{ name }} (0 / 100)</div>
        <PrimaryButton>Enter Challenge</PrimaryButton>
      </div>
    </div>
    <div>
      <DescriptionDisplay
        class="c-challenge-description"
        :config="config"
      />
      <br>
      <span class="c-challenge-description">Entering challenge will force {{ resetType }} reset.</span>
      <br>
      <span>Goal: </span>
      <br>
      <EffectDisplay
        class="c-challenge-effect"
        :config="config"
      />
      <DescriptionDisplay
        class="c-green"
        :config="reward"
        title="Reward:"
        br
      />
      <EffectDisplay
        class="c-green"
        :config="reward"
        br
      />
    </div>
  </div>
</template>

